import { NextRequest, NextResponse } from 'next/server';
import { isUserExist } from "@/utils/authUtils";
import { saveFilesFromFormData } from '@/utils/saveFiles';
import { validateFormData } from '@/utils/validateFormData';
import { createProduct } from '@/app/api/models/product';
import path from 'path';

export async function POST(req: NextRequest) {
  try {
    // Retrieve x-admin-id from request headers
    const adminId = req.headers.get("x-admin-id");
    const adminRole = req.headers.get("x-admin-role");
    if (!adminId || isNaN(Number(adminId))) {
      return NextResponse.json(
        { error: "User ID is missing or invalid in request" },
        { status: 400 }
      );
    }

    // Check if admin exists
    const result = await isUserExist(Number(adminId), String(adminRole));
    if (!result.status) {
      return NextResponse.json({ error: `User Not Found 1: ${result.message}` }, { status: 404 });
    }

    const isMultipleImages = false; // Set to true if you want to allow multiple images

    const formData = await req.formData();

    const validation = validateFormData(formData, {
      requiredFields: ['name', 'price', 'quantity'],
      patternValidations: {
        price: 'number',
        quantity: 'number',
        status: 'boolean',
      },
    });

    if (!validation.isValid) {
      return NextResponse.json({ success: false, error: validation.errors }, { status: 400 });
    }

    const name = formData.get('name') as string;
    const description = formData.get('description') as string;
    const price = parseFloat(formData.get('price') as string);
    const quantity = parseInt(formData.get('quantity') as string, 10);
    const status = formData.get('status') === 'true';

    const uploadPath = path.join(process.cwd(), 'tmp', 'uploads', 'products');
    const fileData = await saveFilesFromFormData(formData, 'image', {
      dir: uploadPath,
      pattern: 'slug-unique',
      multiple: isMultipleImages,
    });

    // Create a final string of URLs separated by commas in both conditions
    const image = isMultipleImages ? fileData.map(file => file.url).join(', ') : fileData.url;

    const productData = {
      name,
      description,
      price,
      quantity,
      status:
        typeof status === 'string'
          ? ['true', '1'].includes(status.toLowerCase())
          : !!status,
      image,
    };
    console.log(`productData - `, productData);
    await createProduct(Number(adminId), productData);

    return NextResponse.json({ success: true, files: fileData });
  } catch (err) {
    console.error('‚ùå File Save Error:', err);
    return NextResponse.json({ success: false, error: 'File upload failed' }, { status: 500 });
  }
}
